/*
Theme Name: Rockin' With Purpose
Author: Tim Fau
Description: Custom theme built for Rockin' With Purpose online blog/magazine.
Version: 2
Last Modified: 2/9/19
Tags: RWP, Tim Fau
*/
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

.clearfix {
  display: inline-block; }

/* Hides from IE-mac \*/
* html .clearfix {
  height: 1%; }

.clearfix {
  display: block; }

/* End hide from IE-mac */
/* ------------------------------------------------------------------------------------------------------------------------------
  General CSS
--------------------------------------------------------------------------------------------------------------------------------- */
* {
  font-family: 'montserrat', sans-serif;
  font-style: normal;
  font-weight: 400; }

p {
  font-size: 14px; }

h2 {
  font-size: 26px;
  color: white;
  line-height: 1.2em; }

h2.box {
  text-shadow: 2px 2px 1px black; }

h3 {
  font-size: 22px;
  color: #333333;
  font-weight: 600; }

a {
  text-decoration: none;
  word-wrap: break-word;
  overflow-wrap: break-word; }

.post a {
  color: #993333; }

.post a:hover {
  text-decoration: underline; }

body {
  margin: 0 0;
  background-color: #f2f2f2;
  background-image: url(../../img/grunge_wall.png);
  overflow-x: hidden;
  width: 100%; }

body a:hover {
  text-decoration: none; }

ul {
  list-style: none; }

img {
  width: 100%;
  height: 100%;
  display: block; }

.postcategory {
  font-size: 11px;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-shadow: 0px 0px;
  display: inline; }

.postcategory a {
  background: #993333;
  padding: 3px 7px;
  opacity: 0.8; }

/* Hide for Mobile */
.mnone, .leader {
  display: none; }

/* Uncategorized */
.contentarea img {
  width: 100%;
  height: 100%; }

.contentarea p {
  color: white; }

.hpheading {
  color: white;
  text-align: center;
  background-color: #333333;
  border-top: 6px solid #cccccc;
  border-bottom: 6px solid #cc0033;
  padding: 26px 0px 6px 0px;
  margin: 0 0;
  line-height: 22px; }

/* ------------------------------------------------------------------------------------------------------------------------------
  Featured Content Area
--------------------------------------------------------------------------------------------------------------------------------- */
.featuredcontent {
  margin-top: -20px;
  overflow: hidden; }

.ft_content {
  position: relative;
  display: block;
  overflow: hidden;
  float: left;
  width: 100%;
  padding: 0 0;
  margin: 0 0; }

.ft_content img {
  height: 100%;
  width: 100%; }

.ft_desc {
  position: absolute;
  bottom: 0;
  padding: 0 15px; }

.ft_content:first-child h2 {
  margin-bottom: 10px; }

.ft_content:not(:first-child) {
  width: 50%;
  height: 50%; }

.ft_content:not(:first-child) h2 {
  font-size: .8em; }

.ft_content:hover img, .content:hover img {
  transform: scale(1.1);
  transition: 1s; }

.ft_content:hover .ft_desc, .content:hover .desc {
  transform: rotate(-2deg);
  background: black;
  opacity: 0.8;
  transition: 1s; }

.postcategory a {
  color: white; }

.featuredcontent h2 {
  margin-top: 10px; }

/* ------------------------------------------------------------------------------------
    Remaining content general styles
 --------------------------------------------------------------------------------------- */
.not_featured {
  clear: both; }

/* ------------------------------------------
  Recent Content
--------------------------------------------- */
.recent_item {
  position: relative;
  overflow: hidden; }

.recent_desc {
  position: absolute;
  bottom: 0;
  padding: 8px 8px 0 8px;
  background: black;
  opacity: 0.8;
  width: 100%;
  box-sizing: border-box; }

.recent_desc h2 {
  margin: 8px 0 0 0; }

.recent_desc p {
  margin: 0 0 5px 0; }

.recent_desc .postcategory a {
  margin-top: 100px; }

/* Transformation Effects */
.recent_item:hover .ri_img img {
  transform: scale(1.05);
  transition: 1s; }

.recent_item:hover .recent_desc {
  opacity: .9;
  transition: 1s; }

/* ------------------------------------------
  New Releases
--------------------------------------------- */
.newreleases {
  position: relative;
  width: 100%;
  display: inline-block; }

.album {
  width: 50%;
  float: left;
  overflow: hidden; }

.albumdesc {
  position: absolute;
  top: 40%;
  left: 40%;
  display: none; }

/* Transformation Effects */
.album:hover img {
  transform: scale(1.1);
  transition: 1s; }

/* ------------------------------------------
  Upcoming Artists
--------------------------------------------- */
.upcomingartist {
  position: relative;
  display: inline-block;
  width: 100%;
  overflow: hidden; }

.ua_desc {
  position: absolute;
  bottom: 0;
  padding: 0 10px;
  width: 100%; }

/* Transformation Effects */
.upcomingartist:hover img {
  transform: scale(1.1);
  transition: 1s; }

.upcomingartist:hover .ua_desc {
  transform: rotate(-2deg);
  background: black;
  transition: 1s; }

/* ------------------------------------------------------------------------------------
  Footer
--------------------------------------------------------------------------------------- */
.footer {
  background: #333333;
  color: #999999;
  text-align: center;
  padding: 33px 0;
  margin: 0px 0 0 0;
  width: 100%;
  clear: both;
  border-top: 6px solid #cccccc; }

.footer ul {
  list-style-type: none;
  padding-left: 0; }

.footer li {
  display: inline-block; }

.footer li a {
  color: white;
  font-size: .8em;
  padding: 0 8px; }

/* ------------------------------------------------------------------------------------

  Media Queries

--------------------------------------------------------------------------------------- */
@media screen and (min-width: 600px) {
  .ft_content:first-child h2 {
    font-size: 2.8em; }
  .ft_content:not(:first-child) h2 {
    font-size: 1.2em; }
  .contentarea p {
    font-size: 1em; } }

@media screen and (min-width: 900px) {
  /* Font Sizes */
  .contentarea p {
    font-size: .9em; }
  .ft_content:first-child h2 {
    font-size: 2.2em; }
  h3.hpheading_hp, h2.hpheading {
    text-shadow: 1px 1px 1px black;
    font-weight: 600; }
  /* General Layout/Etc. */
  .not_featured {
    margin-left: 2.5%;
    margin-right: 2.5%; }
  .contentarea {
    width: 100%;
    margin: 0 auto;
    display: block; }
  .invisible {
    width: 100%;
    display: inline-block; }
  .dhide {
    display: none; }
  .mnone {
    display: block; }
  .hpheading {
    color: #333333;
    text-align: left;
    background: none;
    border-top: 0px;
    border-bottom: 0px;
    padding: 30px 0px 10px 0px;
    margin: 0 0; }
  .post p {
    padding-right: 50px; }
  /* ------------------------------------------
    Featured Content Area -TF
    --------------------------------------------- */
  .ft_content:first-child {
    width: 50%;
    height: 50%; }
  .ft_content:not(:first-child) {
    width: 25%;
    height: 25%; }
  /* ------------------------------------------
      Recent News & Features -TF
    --------------------------------------------- */
  .recentnews {
    text-align: justify;
    position: relative; }
  .recent_item {
    width: 32.5%;
    height: 32.5%;
    display: inline-block;
    text-align: left; }
  .l_container {
    position: relative;
    display: inline-block;
    overflow: hidden;
    width: 100%;
    margin-top: 25px; }
  .hpheading {
    position: relative;
    display: inline-block;
    padding-right: 20px;
    letter-spacing: 4px;
    text-transform: uppercase; }
  .hpheading::after {
    content: "";
    display: block;
    height: 6px;
    position: absolute;
    width: 1920px;
    margin-top: 7px;
    top: 50%;
    left: 100%;
    background-color: #333333;
    box-shadow: 1px 1px 1px black; }
  .headingbox {
    padding-bottom: 15px;
    overflow: hidden; }
  .headingbox p {
    color: #666666;
    text-transform: uppercase;
    font-weight: 600;
    padding-top: 5px; }
  /* ------------------------------------------
      New Releases -TF
    --------------------------------------------- */
  .album {
    width: 19%;
    height: 19%;
    margin-right: 1.25%;
    position: relative; }
  .album:nth-of-type(6) {
    margin-right: 0%; }
  .albumdesc {
    top: 30%;
    left: 20%;
    display: none; }
  .album:nth-child(7) {
    display: none; }
  /* ------------------------------------------
      Upcoming Artists -TF
    --------------------------------------------- */
  .upcomingartist {
    width: 32.5%;
    height: 32.5%;
    margin-right: 1.25%;
    float: left; }
  .upcomingartist:nth-of-type(4) {
    margin-right: 0; }
  .upcomingartist img {
    width: 100%;
    height: 100%; }
  .ua_desc {
    width: 100%;
    padding-bottom: 0px;
    display: inline-block;
    box-sizing: border-box; }
  .ua_desc h2 {
    font-size: 150%;
    text-align: center; } }

@media screen and (max-width: 1000px) {
  .featuredcontent .postcategory a {
    display: none; } }

/* ------------------------------------------
      1200px+ -TF
--------------------------------------------- */
@media screen and (min-width: 1300px) {
  .featuredcontent, .not_featured, .navbar .container, .cat_header_post_float, #blogcontentarea {
    width: 1200px;
    margin: 0 auto; }
  .contentarea {
    display: block; }
  .navlogo {
    margin: 1.5% 0 0 .5%; }
  .navigation {
    margin: 15px 0 0 0; }
  /* ------------------------------------------
    Font Sizes
    --------------------------------------------- */
  .ft_content:first-child h2 {
    font-size: 2.8em; }
  .content h2 {
    font-size: 130%; } }
