/* ------------------------------------------------------------------------------------------------------------------------------
  Header
--------------------------------------------------------------------------------------------------------------------------------- */
.navbar {
  padding: 35px 0px;
  margin-bottom: 20px;
  border-bottom: 6px solid #cccccc;
  background-color: black;
  box-shadow: 0 0 5px black; }
  @media screen and (min-width: 900px) {
    .navbar {
      width: 100%;
      display: inline-block;
      padding: 5px 0 8px 0;
      padding: 0;
      margin-bottom: 16px; } }

.navlogo {
  width: 140px;
  margin: 0 auto;
  max-width: 17%; }
  @media screen and (min-width: 900px) {
    .navlogo {
      float: left;
      margin-left: 2.5%;
      padding: 1% 0; } }

/* Navigation */
nav.navbar ul.sub-menu {
  display: none;
  position: absolute;
  left: 0;
  padding-left: 0;
  background: black;
  z-index: 1;
  box-shadow: 6px 12px 25px black; }
  nav.navbar ul.sub-menu li {
    display: block; }
    nav.navbar ul.sub-menu li:hover {
      background: black; }
    nav.navbar ul.sub-menu li a {
      color: white;
      white-space: nowrap; }

nav.navbar li {
  position: relative; }
  nav.navbar li:hover a {
    color: #d01d33; }
  nav.navbar li:hover .sub-menu {
    display: block; }
  nav.navbar li a {
    text-align: center;
    transition: 0.3s;
    font-size: 20px;
    margin: 10px; }
  @media screen and (min-width: 900px) {
    nav.navbar li {
      display: inline-block;
      margin-right: 0px;
      padding: 8px 4px; } }

nav.navbar .followBTN {
  display: none; }
  @media screen and (min-width: 900px) {
    nav.navbar .followBTN {
      height: 35px;
      color: white;
      width: 50%;
      float: right;
      padding-right: 15px; }
      nav.navbar .followBTN img {
        width: auto;
        float: right; }
        nav.navbar .followBTN img:hover {
          opacity: .8; } }

@media screen and (min-width: 900px) {
  nav.navbar .navigation {
    width: 80%;
    display: inline-block;
    padding: 2% 0; }
    nav.navbar .navigation li a {
      color: white;
      text-transform: uppercase;
      font-size: 18px;
      font-weight: 600; }
      nav.navbar .navigation li a:hover {
        color: #d41733; } }
